import React from "react"
import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"

const Contact = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Contact App store" />
      <Bio />
      <h1><strong>CONTACT US</strong></h1><hr/>
<h3>SHOWROOM</h3>
<p>Ruko Pasar Wisata Juanda Blok Q.05-07<br/>Sidoarjo - East Java - Indonesia<br/>
Marketing : +6281259444676 +6285331361404 +6287852476342<br/>
WhatsApp +6285646104747<br/>
Email : hockeycorpmarketing@gmail.com</p>
<a href="https://wa.me/6285646104747" class="button" target="blank" rel="nofollow">Chat Now</a> <a href="tel:+6285331361404" target="blank" class="button" rel="nofollow">Call Us</a> <a href="https://goo.gl/maps/V2Wxp2RcbF3cHxFC6" class="button" target="blank" rel="nofollow">Maps</a>
    </Layout>
  )
}

export default Contact
export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`